


import { Component, OnInit, EventEmitter, Output, Input, AfterViewInit, OnChanges, SimpleChanges, ChangeDetectorRef } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AuthService } from 'src/app/shared/services/Index';
import { map, tap, catchError, shareReplay, debounceTime, distinctUntilChanged, switchMap } from 'rxjs/operators';
import { Router } from '@angular/router';
import { BehaviorSubject, Observable, of, throwError } from 'rxjs';
import { Role } from 'src/app/shared/models/Role';
import { UserService } from 'src/app/shared/services/user/user.service';
import { FileService } from 'src/app/shared/services/client/file.service';
import { DeviceService } from 'src/app/shared/services/client/device.service';
import { JobOpening } from 'src/app/shared/models/JobOpening';
import { JobService } from 'src/app/shared/services/recruitment/job.service';
import { JobApplication } from 'src/app/shared/models/JobApplication';
import { ScriptsService } from 'src/app/shared/services/client/scripts.service';
import { JobPosition } from 'src/app/shared/models/JobPosition';
import { Department } from 'src/app/shared/models/Department';
import { DepartmentService } from 'src/app/shared/services/department/department.service';
import { CompanyService } from 'src/app/shared/services/company/company.service';
import { User } from 'src/app/shared/models/User';

declare const $: any;
@Component({
  selector: 'app-employee-update',
  templateUrl: './update.component.html',
  styleUrls: ['./update.component.scss']
})
export class UpdateComponent implements OnInit, AfterViewInit, OnChanges {
  @Input('role') role: string = '';
  @Input('showTitle') showTitle: boolean = false;
  @Input('loadForm') loadForm: boolean = false;
  smartwizardupdateEmployee: any = null;
  fileToUpload: any = null;
  searchKey: any = '';
  status: any = '';
  status_update: any = '';
  opening_code: any = null;
  formStep: number = 0;
  isChecked = false;
  isLinear = true;
  url: string = '';
  saved = false;
  message = '';
  errorMessage = '';
  country: any = {
    search: ''
  }
  @Input('newEmployee') newEmployee: any = {
    code: '',
    firstName: '',
    lastName: '',
    gender: '',
    marital_status: '',
    country: '',
    phone: '',
    address: '',
    state: '',
    city: '',
    date_of_employment: '',
    dob: '',
    email: '',
    password: '',
    passport: '',
    role: '',
    job_title: '',
    job_positions: '',
    // reports_to: '',
    department: '',
    type_of_contract: '',
    working_hours: '',
    cfale: '',
    ale: '',
    ala: '',
    sort_code: '',
    location: '',
    probation: '',
    confidance: '',
    result: '',
    comments: '',
    behavior: '',
    amount: '',
    currency: '',
    pay_type: '',
    payroll: '',
    pension: '',
    name_of_acc: '',
    name_of_bank: '',
    acc_num: '',
  }
  totalFields: number = 36;
  positions$: Observable<JobPosition[]> = this._js.getJobPositionsApi$.pipe(
    tap(r=>{
      this.changeDetect.detectChanges();

    }),
    shareReplay()
  );
  departments$: Observable<Department[]> = this._depServ.getDepartmentsApi$.pipe(
    shareReplay()
  );
 private  _job_applications: BehaviorSubject<Map<string, JobApplication[]>> = this._js.job_applications;

  assignableRole: Observable<Role[]> = this._us.roles$.pipe(
    map((roles: Role[]) => {
      return roles.filter(r => r.name != 'Master')
    }),
    tap(r=>{
      this.newEmployee.role = r.find(_r=>_r.name == 'Staff')?.name;
    })
  );
  @Output() hasSaved: EventEmitter<User> = new EventEmitter<User>();
  result: any[] = [];
  employeeClone:any = null;
  constructor(private _ds: DeviceService,
    private _depServ: DepartmentService,
    private _script: ScriptsService,
    private _js: JobService,
    private _us: UserService,
    private changeDetect: ChangeDetectorRef,
    private _company: CompanyService, private auth: AuthService, private changeDetRef: ChangeDetectorRef, private router: Router, private _fs: FileService) { }
  ngOnChanges(changes: SimpleChanges): void {
    if (this.newEmployee) {
      this.employeeClone = {...this.newEmployee};
      if (changes.loadForm.currentValue) {
        this.smartwizardupdateEmployee = $(`#smartwizardupdateEmployee${this.newEmployee.code}`);
        this.smartwizardupdateEmployee.smartWizard({
          selected: this.formStep, // Initial selected step, 0 = first step
          // theme: 'default', // theme for the wizard, related css need to include for other than default theme
          justified: true, // Nav menu justification. true/false
          autoAdjustHeight: true, // Automatically adjust content height
          backButtonSupport: true, // Enable the back button support
          enableUrlHash: true, // Enable selection of the step based on url hash
          transition: {
            animation: 'slideHorizontal', // Animation effect on navigation, none|fade|slideHorizontal|slideVertical|slideSwing|css(Animation CSS class also need to specify)
            speed: '400', // Animation speed. Not used if animation is 'css'
            easing: '', // Animation easing. Not supported without a jQuery easing plugin. Not used if animation is 'css'
            prefixCss: '', // Only used if animation is 'css'. Animation CSS prefix
            fwdShowCss: '', // Only used if animation is 'css'. Step show Animation CSS on forward direction
            fwdHideCss: '', // Only used if animation is 'css'. Step hide Animation CSS on forward direction
            bckShowCss: '', // Only used if animation is 'css'. Step show Animation CSS on backward direction
            bckHideCss: '', // Only used if animation is 'css'. Step hide Animation CSS on backward direction
          },
          toolbar: {
            position: 'bottom', // none|top|bottom|both
            showNextButton: true, // show/hide a Next button
            showPreviousButton: true, // show/hide a Previous button
            extraHtml: `
            <button class="btn btn-danger" onclick="clickId('resetEmployeeFormBtnHidden')">Reset</button>
            <button class="btn btn-success" id="canisubmit64${this.newEmployee?.code}" onclick="clickId('createEmployeeSubmitBtnHidden${this.newEmployee?.code}')">
            Update</button>` // Extra html to show on toolbar
          },
          anchor: {
            enableNavigation: true, // Enable/Disable anchor navigation
            enableNavigationAlways: false, // Activates all anchors clickable always
            enableDoneState: true, // Add done state on visited steps
            markPreviousStepsAsDone: true, // When a step selected by url hash, all previous steps are marked done
            unDoneOnBackNavigation: false, // While navigate back, done state will be cleared
            enableDoneStateNavigation: true // Enable/Disable the done state navigation
          },
          keyboard: {
            keyNavigation: true, // Enable/Disable keyboard navigation(left and right keys are used if enabled)
            keyLeft: [37], // Left key code
            keyRight: [39] // Right key code
          },
          lang: { // Language variables for button
            next: 'Next',
            previous: 'Previous'
          },
          disabledSteps: [], // Array Steps disabled
          errorSteps: [], // Array Steps error
          warningSteps: [], // Array Steps warning
          hiddenSteps: [], // Hidden steps
          getContent: (idx: any, stepDirection: any, stepPosition: any, selStep: any, callback: any) => this.provideContent(idx, stepDirection, stepPosition, selStep, callback) // Callback function for content loading
        });
        this.smartwizardupdateEmployee.smartWizard("reset");
        this.smartwizardupdateEmployee.on("loaded", (e:any) =>{

          this.load(this.newEmployee);
        });

      }
    }
  }
  ngAfterViewInit(): void {

  }

  ngOnInit(): void {
    this.url = this.router.url;
  }
  provideContent(idx: any, stepDirection: any, stepPosition: any, selStep: any, callback: any) {
    this.formStep = idx;
    const canisubmit64: any = document.getElementById("canisubmit64"+this.newEmployee?.code);
    // You can use stepDirection to get ajax content on the forward movement and stepPosition to identify the step position
    if (stepDirection == 'forward' && stepPosition == 'middle') {
      let ajaxURL = "YOUR AJAX URL";

    }
    if (this.formStep == 3) {
      canisubmit64.classList.remove('d-none')
    } else {
      canisubmit64.classList.add('d-none')
    }

    // The callback must called in any case to procced the steps
    // The empty callback will not apply any dynamic contents to the steps
    callback();
  }
  changedOpening(value: any) {
    //
    this.searchKey = '';
  }
  changeSerarchKey(value: any) {
    this.searchKey = value;
    if(this.searchKey.length>3 || !this.job_applications.get(this.searchKey)){
      this.status = 'loading';
      this._js.searchJobOpeningApplicationsApi(this.opening_code,this.searchKey)
      .pipe(
        debounceTime(2000),
        distinctUntilChanged(),
      ).subscribe(c=>this.status = '')
    }
  }
  clickedUploadFile() {
    const formFile: any = document.getElementById('formFile');
    //
    formFile.click()
  }
  reset() {
    this.newEmployee = this.employeeClone;
    this.smartwizardupdateEmployee.smartWizard("goToStep", 0, true);
    this.hasSaved.emit(this.newEmployee);
    this._ds.oInfoNotification('Data Reset', 'Data restored to last saved state')
  }

  load(applicant:any){
    this.smartwizardupdateEmployee.smartWizard("goToStep", 0, true);
    const selectDropdown = $(`.selectpicker-update-position-${this.newEmployee?.code}`);
    this.newEmployee = {
      ...this.newEmployee,
      firstName: applicant.firstName,
      lastName: applicant.lastName,
      country: applicant.country,
      phone: applicant.phone,
      email: applicant.email
    };
    this.status_update = applicant.status;
    this.country.search =  applicant.country;
    this.searchKey = '';
    this.opening_code= '';
    this._ds.oInfoNotification('Data loaded', 'Employee data loaded successfully');
    this.positions$.subscribe(r=>{
      selectDropdown.selectpicker();
      selectDropdown.on('changed.bs.select', this.handleJobPosition);
    });
  }
  handleJobPosition(e:any, clickedIndex:any, isSelected:any, previousValue:any){




  }
  resetConfirmation() {
    $('#danger-modal-'+this.newEmployee.code).modal('show');
  }
  handleFileInput(event: any) {
    const files: FileList = event.target.files;
    this.fileToUpload = files.item(0);
    //
    let formData = new FormData();
    formData.append('file', this.fileToUpload, this.fileToUpload?.name)
    this._fs.upload(formData)
      .pipe(
        catchError((err: any) => {

          return throwError(err);
        })
      )
      .subscribe(
        f => {
          this.newEmployee.passport = f.file;

        }
      )

  }
  updateImageUploaded() {
    const passportEmployeeephotoId: HTMLInputElement = <HTMLInputElement>document.getElementById("passportEmployeeephotoId");
    passportEmployeeephotoId.click();
  }
  submit() {

    this.errorMessage = ``;
    let errF = '';
    this.assignableRole
    .pipe(
      switchMap(
        (r:any)=>{
          for (const [key, value] of Object.entries(this.newEmployee)) {
            const v: any = value;
            if (!v || v.length == 0) {
              this.errorMessage += `${key.replace('_', ' ').toUpperCase()} required, `;
              errF += `${key}, `
            }
          }
          if (this.errorMessage.length > 0) {
            const notification = this._ds.openErrorNotification('Opps..', `Please reconfirm form, these fields: ${errF} might have been left empty by you which is required.`);
            return of(
              {
                status:'500',
                notification
              }
            )
          } else {
            let user = {
              ...this.newEmployee,
              status:this.status_update,
            }
            const notification = this._ds.oInfoNotification('Update Submitted', `Your request was submitted successfully and is currently being processed.`);
            this.hasSaved.emit(user);
            return this._us.updateUser(user)
            .pipe(
              catchError((e:any)=>{
                this.handleError(e, 'updating user');
                return throwError(e);
              }),
              map(data=>data),
              tap((r:any)=>{
                this.hasSaved.emit(r.user);
                this.saved = true
              })
            )
          }
        }
      )
    )
    .subscribe(
      (r:any)=>{
        // this._ds.openSuccessNotification('Employee Created', 'Saving Employee info to the database..');
        if(r.status == '200'){
          this._ds.openSuccessNotification('Employee Data Updated', `${r.user.firstName} ${r.user.lastName} record was successfully updated`);
        }else{
          this._ds.oErrorNotification('Opps...', r.response);
        }
                  // this.reset()
                  // this.hasSaved.emit(r.user);
      }
    )

  }

  handleError(err:any , action:any){

    const norification = this._ds.openErrorNotification('Opps..', `Issues with ${action} action`);
  }

  get job_applications(){
    return this._job_applications.value
  }
  get applications(){
    const app =  this.job_applications.get(this.opening_code)?.filter(
      a=>{
        if(this.searchKey.length < 1){
          return a.status == 'Accepted' || a.status == 'Employed';
        }
        return (a.status == 'Accepted' || a.status == 'Employed') && a.code.toLowerCase().includes(this.searchKey.toLowerCase())
      }
      );
    if (!app){
      return [];
    }
    return app;
  }

}
