import { Component, OnInit } from '@angular/core';
import { Observable, ReplaySubject } from 'rxjs';
import { map, tap, shareReplay } from 'rxjs/operators';
import { User } from 'src/app/shared/models/User';
import { UserService } from 'src/app/shared/services/user/user.service';
import {animate, state, style, transition, trigger} from '@angular/animations';
import { Location } from '@angular/common';

@Component({
  selector: 'app-employee-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ]
})
export class ListComponent implements OnInit {
  dataSource:ReplaySubject<User[]> = new ReplaySubject<User[]>();

  users$: Observable<User []> = this._us.getEmployees({_sort: "created_at", _order: 'desc', _limit: 500}).pipe(
    map(
      (r:any)=>{

        this.users = r;
        return r
      }
    ),
    shareReplay()
  );
  users:User [] = [];
  p:any =0;
  appFetchStatus: string = 'Yet';
  columnsToDisplay = ['code','firstName', 'lastName', 'gender', 'dob', 'department', 'status'];
  columnsToDisplayWithExpand = [...this.columnsToDisplay, 'expand'];
  expandedElement: User | null = null;

  constructor(private _us: UserService,private location: Location) { }

  ngOnInit(): void {
    this.users$.subscribe(
      r=>this.dataSource.next(r)
    );
  }

  translate(key:string){
    return `${key.replace('_', ' ')}`;
  }
  copied(url:string){
    // let opt = {...this._ds.toastOptions, theClass: 'welcome-modal', timeOut:3000 };
    // this._ds.oSuccessNotification('Copied Job Opening URL', `${url} copied to clipboard.`, opt);
  }
  applicationEmitter(event:any, index:any, application:any){
    application = event;
  }
  dropingDown(element:any, event:any){
    if(this.expandedElement === element){
      this.expandedElement = null
    }else{
      this.expandedElement = element;
      this.getCurrentApplication()
    }
    event.stopPropagation();
  }

  getCurrentApplication(){
    // if(this.expandedElement){
    //   const jobApps = this._js.job_applications.value;
    //   this.appFetchStatus = 'Fetching job applications...'
    //   this._js.getJobOpeningApplicationsApi(this.expandedElement.code).subscribe(applications=>this.appFetchStatus = 'Loaded'
    //   )
    // }
  }
  updatedUserEventHandler(event:any, element:any){
    this.expandedElement = {...this.expandedElement, ...event};
    const previousList = this.users;
    const indexOfChanged = previousList.findIndex((u:any)=>u.code == event.code);
    if(indexOfChanged > -1){
      previousList.splice(indexOfChanged,1);
    }
    previousList.unshift(<User>this.expandedElement);
    this.dataSource.next(previousList);

  }
}
